  <v-container class="pa-0">
    <TopToolbarSelection :backwardRoute="'FlavourMerger'"
                         :fusionHistoryProp="fusionHistoryProp">
      <template v-slot:additionalNavigation>
        <v-btn icon color="black">
          <v-icon>mdi-magnify</v-icon>
        </v-btn>
        <v-btn icon color="black" @click="orderBySensation = !orderBySensation">
          <v-icon dark>{{orderBySensation ? 'mdi-order-alphabetical-ascending' : 'mdi-order-bool-ascending'}}</v-icon>
        </v-btn>
      </template>
    </TopToolbarSelection>
    <v-expansion-panels flat
                        multiple
                        accordion
                        v-model="panel">
      <template v-if="orderBySensation" class="w-100">
        <v-expansion-panel
            v-for="(item,i) in orderedIngredients"
            :key="i"
            :readonly="!item.list.length"
        >
          <v-expansion-panel-header class="pa-0">
            <v-icon class="panel-circle mr-2" :class="item.class">
              mdi-circle
            </v-icon>
            <img v-if="item.img" style="max-width: 20px; max-height: 20px" :alt="item.title" :src="item.img">
            <p :class="$vuetify.breakpoint.xsOnly
                                 ? 'subtitle-1 text-uppercase mb-0'
                                 : 'title text-uppercase mb-0'"
               :style="!item.list.length ? 'color: rgba(0, 0, 0, 0.54)' : ''">
              {{ item.title }}</p>
            <template v-if="!item.list.length" v-slot:actions>
              <span style="color: rgba(0, 0, 0, 0.54)" class="overline mr-1">leer</span>
            </template>
          </v-expansion-panel-header>
          <v-expansion-panel-content v-if="item.list.length" class="py-0">
            <CardList :itemList="item.list"
                      :selectionSliders="selectionSliders"
                      :cardIndex="cardIndex"
                      :fusionHistoryProp="fusionHistoryProp"/>
          </v-expansion-panel-content>
        </v-expansion-panel>
      </template>
      <div class="w-100" v-else>
        <CardList :itemList="ingredientList"
                  :selectionSliders="selectionSliders"
                  :cardIndex="cardIndex"
                  :fusionHistoryProp="fusionHistoryProp"/>
      </div>
      <v-expansion-panel v-if="noAromaIngredients.length && selectionSliders.showAroma && orderBySensation"
                         class="w-100">
        <v-expansion-panel-header class="pa-0">
          <p :class="$vuetify.breakpoint.xsOnly
                                 ? 'subtitle-1 text-uppercase mb-0'
                                 : 'title text-uppercase mb-0'">Ohne Aromen</p>
        </v-expansion-panel-header>
        <v-expansion-panel-content class="pa-0">
          <card-list :itemList="noAromaIngredients"
                     :selectionSliders="selectionSliders"
                     :cardIndex="cardIndex"
                     :fusionHistoryProp="fusionHistoryProp"/>
        </v-expansion-panel-content>
      </v-expansion-panel>
      <v-expansion-panel v-if="fusionIngredients.length" class="w-100">
        <v-expansion-panel-header class="pa-0">
          <p :class="$vuetify.breakpoint.xsOnly
                                 ? 'subtitle-1 text-uppercase mb-0'
                                 : 'title text-uppercase mb-0'">Bereits ausgewhlt</p>
        </v-expansion-panel-header>
        <v-expansion-panel-content class="pa-0">
          <CardList :itemList="fusionIngredients"
                    :selectionSliders="selectionSliders"
                    :cardIndex="cardIndex"
                    :fusionHistoryProp="fusionHistoryProp"/>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
    <v-bottom-navigation
        app
        horizontal
        height="48px"
        fixed>
      <BottomNavigationSelector
          :selectionSliders.sync="selectionSliders"/>
    </v-bottom-navigation>
  </v-container>
</template>

<script>
import TopToolbarSelection from '@/components/TopToolbarSelection'
