                               :class="$vuetify.breakpoint.xsOnly ? '' : 'text-center'">
                          <v-avatar
                              :class="$vuetify.breakpoint.xsOnly
                              ? 'mr-3' : ''"
                              color="rgb(210, 210, 249)"
                              :size="$vuetify.breakpoint.xsOnly
                              ? '30' : '56'"
                          ></v-avatar>
                          <span v-if="fusionIngredients[n-1]" class="text-uppercase title black--text hidden-sm-and-up">
                            {{fusionHistory[fusionIndex][n-1].name}}
                          </span>
                          <p v-if="fusionIngredients[n-1]" class="text-uppercase title black--text hidden-xs-only mt-5">
                            {{fusionHistory[fusionIndex][n-1].name}}
                          </p>
                        </v-col>
                        <v-col :cols="$vuetify.breakpoint.xsOnly ? '2' : '12'"
                               :class="$vuetify.breakpoint.xsOnly ? '' : 'text-center'">
                          <v-btn icon
                                 :input-value="fusionHistory[fusionIndex][n-1].active"
                                 @click.prevent="fusionHistory[fusionIndex][n-1].active = !fusionHistory[fusionIndex][n-1].active">
                            <v-icon>
                              {{ fusionHistory[fusionIndex][n-1].active ? 'mdi-lock' : 'mdi-lock-open-outline' }}
                            </v-icon>
                          </v-btn>
                        </v-col>
                      </v-row>
                    </v-card-title>
                    <v-card-title class="fill-height sheet-color justify-center" v-else>
                      <v-row>
