            <v-btn
                icon
                color="rgba(28, 28, 225, 0.8) !important"
                :disabled="!canUndo"
                @click="fusionIndex--"
            >
              <v-icon>mdi-undo</v-icon>
            </v-btn>
            <v-btn
                icon
                color="rgba(28, 28, 225, 0.8) !important"
                :disabled="!canRedo"
                @click="fusionIndex++"
            >
              <v-icon>mdi-redo</v-icon>
            </v-btn>
            <v-btn class="text-uppercase ml-auto"
                   outlined
                   @click="generateAF"
            >generieren</v-btn>
          </v-card-actions>
        </v-card>
        <BottomNavigationSelector
            :hide-senses="true"
            :selectionSliders.sync="selectionSliders">
        </BottomNavigationSelector>
        <v-row justify="center">
          <v-btn class="text-uppercase app-primary"
                 fixed bottom
                 outlined
                 :disabled="fusionIndex === 0"
                 @click="sheet = !sheet"
          >fusionieren</v-btn>
        </v-row>
      </v-item-group>
    </v-container>
    <div class="text-center rounded-t-xl">
      <v-bottom-sheet
          v-model="sheet"
          inset
          class="rounded-t-xl">
        <v-sheet
            class="text-center rounded-t-xl"
            height="500px">
          <v-row
              class="rounded-t-xl"
              no-gutters>
            <v-btn
                class="mt-6 ml-auto"
                text
                @click="sheet = !sheet">
              <v-icon dark>mdi-close</v-icon>
            </v-btn>
          </v-row>
          <v-container
              class="pa-0"
              style="height: 350px"
              no-gutters @click="showAromaFusion = !showAromaFusion, key++">
            <base-polar-area :key="key"
                             :fusionPolarData="fusionPolarData"
                             :showAromaFusion="showAromaFusion"
                             style="height: 300px; position: relative"/>
            <v-row no-gutters class="mt-0 pt-3" align="center">
              <v-col cols="7"></v-col>
              <v-col cols="2" align="end">
                <v-icon small class="pl-13">
                  mdi-information-outline
                </v-icon>
              </v-col>
              <v-col class="caption" cols="2">
                Tippen fr {{ showAromaFusion ? 'Geschmack' : 'Aroma' }}
              </v-col>
            </v-row>
            <v-row justify="center">
              <v-btn class="text-uppercase mx-auto"
                     fixed bottom
                     outlined
                     color="rgba(28, 28, 225, 0.8) !important"
                     :to="{
                           name: 'PreparationSelector',
                           params: {
                             fusionIngredients: fusionIngredients,
                             fusionHistoryProp: fusionHistory
                           }}"
              >Nehm ich!</v-btn>
            </v-row>
          </v-container>
        </v-sheet>
      </v-bottom-sheet>
    </div>
  </div>
</template>

<script>
import TopToolbarSelection from '@/components/TopToolbarSelection'
import BottomNavigationSelector from '@/components/BottomNavigationSelector'
import BasePolarArea from '@/components/BasePolarArea'

export default {
  name: "FlavourMerger.vue",
  components: {
    TopToolbarSelection,
    BottomNavigationSelector,
    BasePolarArea
  },
  props: {
    fusionHistoryProp: Array
  },
  data: () => ({
    selectionSliders: {
      showAroma: true,
      showHarmony: true
    },
    ingredientList: [
      {
        "id": 4,
        "name": "Basilikum",
        "baseImageName": "basil",
        "aroma": [1, 1, 1, 1, 0, 1, 0, 0],
        "taste": [1, 2, 0, 0, 0, 0],
        "background": "Bis vor Kurzem wurde Basilikum einzig mit der mediterranen Kche assoziiert. Seit der Ausbreitung der asiatischen Kche wei man aber: Basilikum wird dort ebenfalls verwendet - zu scharfen Gerichten!",
        "tasteDescription":"Das klassische Basilikum (Genovese) schmeckt angenehm fruchtig-scharf mit sen Tnen.",
        "maxTemperature": 50
      },
      {
        "id": 8,
        "name": "Chilli",
        "baseImageName": "chilli",
        "aroma": [0, 0, 0, 0, 0, 0, 1, 1],
        "taste": [0, 0, 0, 0, 0, 2],
        "background": "Wenn's mal richtig scharf sein darf, greift man auch in Europa immer fter zu Chillis - allzu lange standen diese Scharfmacher hierzuland im Schatten des Pfeffers.",
        "tasteDescription":"Die schrfe wird durch hhere Temperaturen dominanter.",
        "maxTemperature": NaN
      },
      {
        "id": 7,
        "name": "Dill",
        "baseImageName": "dill",
        "aroma": [1, 1, 1, 1, 0, 0, 0, 1],
        "taste": [1, 2, 0, 0, 0, 0],
        "background": "Ursprnglich von den Rmern auf ihren Feldzgen exportiert, gild Dill heute als Kraut des Nordens und des Fisches.",
        "tasteDescription": "Dill erinnert an Kmmel, Anis und Fenchel, schmeckt slich und passt gut zu Fisch und in Sahne- und Mayonnaisesaucen.",
        "maxTemperature": 80
      },
      {
        "id": 6,
        "name": "Minze",
        "baseImageName": "mint",
        "aroma": [1, 0, 1, 0, 0, 0, 1, 0],
        "taste": [0, 1, 0, 0, 0, 1],
        "background": "Aufgrund einer Vielzahl von Kreutzungen existieren zahlreiche verschiedene Arten von Minze, wobei die kulinarisch wichtigste in Europa die grne Minze ist.",
        "tasteDescription":"Minze schmeckt gleichzeitig s und scharf, wirkt aber khlend.",
        "maxTemperature": 70
      },
      {
        "id": 2,
        "name": "Pfeffer",
        "baseImageName": "pepper",
        "aroma": [0, 1, 1, 1, 0, 0, 0, 1],
        "taste": [0, 0, 0, 1, 0, 2],
        "background": "Er gilt als Knig aller Gewrze. Alexander der Groe brachte ihn aus Indien erstmals nach Europa. Schon im rmischen Kochbuch des Apicius kommt er in fast jedem Rezept vor.",
        "tasteDescription":"Pfeffer zeichnet sich neben seiner Schrfe durch erdig-harzige, frische noten aus. Spannend wird es, wenn man unterschiedliche Sorten bewusst einsetzt.",
        "maxTemperature": 120
      },
      {
        "id": 1,
        "name": "Salz",
        "baseImageName": "salt",
        "aroma": [0, 0, 0, 0, 0, 0, 0, 0],
        "taste": [0, 0, 2, 0, 0, 0],
        "background": "Unser Wrzmittel Nummer eins schmeckt nur salzig. Aber es gibt unterschiedliche Sorten, die doch mehr als rein salzig schmecken.",
        "tasteDescription":"Salz passt (in Maen) zu allen Speisen. Bei den in der Kche blichen Temperaturen verndert sich salz nicht.",
        "maxTemperature": NaN
      },
      {
        "id": 3,
        "name": "Zimt",
        "baseImageName": "cinnamon",
        "aroma": [0, 1, 1, 1, 1, 1, 0, 0],
        "taste": [1, 0, 0, 1, 0, 0],
        "background": "Im gypten der Pharaonen wurde Zimt zu rituellen Zwecken eingesetzt. Im Alten Testament galt es als mindestens so wertvoll wie Gold.",
        "tasteDescription":"Zimt riecht sehr s-aromatisch und warm, mit einer erkennbaren herben Note.",
        "maxTemperature": 100
      },
      {
        "id": 5,
        "name": "Zitronenzeste",
        "baseImageName": "lemonzest",
        "aroma": [1, 1, 1, 1, 0, 0, 0, 1],
        "taste": [0, 2, 0, 0, 0, 0],
        "background": "Zitronen sind klassische Suerungsmittel. Ihre Schale bildet die Quintessenz dieser Zitrusfrucht, weil die Aromen hier konzentriert vorliegen.",
        "tasteDescription":"Zitronen duften fruchtig mit vielen Untertnen. Diese Aromen kommen in der Schale besser zur Geltung, im Saft werden sie von den hheren suerlichen anteilen berdeckt.",
        "maxTemperature": 100
      }
    ],
    fusionHistory: [[{}, {}, {}]],
    fusionIndex: 0,
    sheet: false,
    showAromaFusion: true,
    key: 0
  }),
  computed: {
    fusionIngredients () {
      return [...this.fusionHistory[this.fusionIndex]]
    },
    canUndo () {
      return this.fusionHistory.length && this.fusionIndex !== 0
    },
    canRedo () {
      return this.fusionHistory.length
          && this.fusionIndex !== this.fusionHistory.length - 1
    },
    fusionPolarData () {
      return {
        fusionAroma: this.sumFusion('aroma'),
        fusionTaste: this.sumFusion('taste')
      }
    }
  },
  created() {
    if (this.fusionHistoryProp) {
      this.fusionHistory = this.fusionHistoryProp
      this.fusionIndex = this.fusionHistoryProp.length -1
    } else {
      console.log('No fusion history')
    }
  },
  methods: {
    generateAF () {
      const fusionIngredientsUnlocked = !this.fusionIngredients.some(e => e.active)
          ? [...this.ingredientList]
          : this.ingredientList.filter(e =>
              !this.fusionIngredients.some(f => e.id === f.id && f.active ))

      const shuffledfusionIngredientsUnlocked = this.fisherYatesShuffle(fusionIngredientsUnlocked)

      // TODO Forbid the same ingredient at the same position if unlocked
      const newFusion = this.fusionIngredients.map((e,i) =>
          (e.active ? e : {...shuffledfusionIngredientsUnlocked[i], 'active': false})
      )

      this.fusionHistory.push(newFusion)
      this.fusionIndex = this.fusionHistory.length - 1
    },

    fisherYatesShuffle(array) {
      // Fisher Yates Shuffle from https://medium.com/@omar.rashid2/fisher-yates-shuffle-a2aa15578d2f
      let oldElement
      for (let i = array.length - 1; i > 0; i--) {
        let rand = Math.floor(Math.random() * (i + 1));
        oldElement = {...array[i]};
        array[i] = {...array[rand]};
        array[rand] = oldElement;
      }
      return array
    },

    sumFusion (property) {
      this.key++
      const arrayToSum = [...this.fusionHistory[this.fusionIndex]]
          .filter(ingredient => ingredient[property] !== undefined)
          .map(ingredient => (ingredient[property]))
      return arrayToSum.length > 1
          ? arrayToSum.reduce((r, a) => a.map((b, i) => (r[i] || 0) + b), [])
          : arrayToSum[0]
    }
  }
}
</script>

